body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
}

.App {
  text-align: center;
}

.common-padding {
  padding-left: 140px;
  padding-right: 140px;
}

@media (max-width: 1320px) {
  .common-padding {
    padding-left: 80px;
    padding-right: 80px;
  }
}

@media (max-width: 1000px) {
  .common-padding {
    padding-left: 60px;
    padding-right: 60px;
  }
}

@media (max-width: 650px) {
  .common-padding {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.navbar {
  z-index: 998;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #333333;
  color: white;
  padding-top: 20px;
  /* padding-bottom: 10px; */
  box-sizing: border-box;
}

/* Upper Navbar */
.navbar-top {
  display: flex;
  justify-content: space-between;
  /* 좌우 배치 */
  gap: 20px;
  align-items: center;
  width: 100%;
}



.navbar-top-left {
  display: flex;
  flex-grow: 1;
  align-items: center;
}

.logo img {
  height: 30px;
  margin-right: 20px;
  /* 로고와 검색창 간 간격 */
}

.search-bar {
  display: flex;
  align-items: center;
  background-color: #f8f8f8;
  border-radius: 20px;
  padding: 5px 10px;
  width: 100%;
  /* 기본적으로 부모 요소 너비에 맞춤 */
  min-width: 300px;
  max-width: 600px;
  box-sizing: border-box;
  /* 패딩 포함 계산 */
}

.search-bar input {
  border: none;
  outline: none;
  width: 100%;
  padding: 5px;
}

.search-bar .search-btn {
  background: none;
  border: none;
  cursor: pointer;
}

.search-bar .search-btn img {
  width: 16px;
}

.navbar-top-right {
  min-width: 320px;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}


.auth-link {
  width: 66px;
  color: white;
  text-align: center;
  text-decoration: none;
  transition: color 0.3s ease;
  cursor: pointer;
  position: relative;
}

.auth-link::after {
  content: '';
  position: absolute;
  bottom: -10px;
  /* 텍스트 아래에 밑줄 위치 */
  left: 0;
  width: 0;
  height: 2px;
  /* 밑줄 두께 */
  background-color: white;
  /* 밑줄 색상 */
  transition: width 0.3s ease;
}

.auth-link:hover::after {
  width: 100%;
  /* hover 시 밑줄이 전체 너비로 확장 */
}

.sell-btn {
  font-size: 15px;
  width: 167px;
  background: transparent;
  color: white;
  border: 2px solid white;
  padding: 8px 20px;
  border-radius: 20px;
  cursor: pointer;
  transition: background 0.3s, color 0.3s;
}

.sell-btn:hover {
  background-color: white;
  color: #333333;
}

.navbar__hamburger {
  display: none;
  background: transparent;
  border: transparent;
  color: whitesmoke;
  font-size: 20px;
  align-items: center;
}

.menu--open {
  display: none;
}

/* Lower Navbar */
.navbar-bottom {
  display: flex;
  justify-content: space-between;
  /* 카테고리 간 간격 유지 */
  margin-top: 10px;
  /* 상단바와 간격 */
  width: 100%;
}

.category-item {
  position: relative;
  padding: 10px 10px 15px 10px;
  cursor: pointer;
}

.category-link {
  font-size: 16px;
  color: white;
  text-align: center;
  text-decoration: none;
}

.category-item::after {
  content: '';
  position: absolute;
  bottom: 0px;
  /* 텍스트 아래에 밑줄 위치 */
  left: 0;
  width: 0;
  height: 2px;
  /* 밑줄 두께 */
  background-color: white;
  /* 밑줄 색상 */
  transition: width 0.3s ease;
  /* 밑줄 확장 애니메이션 */
}

.category-item:hover::after {
  width: 100%;
  /* hover 시 밑줄이 전체 너비로 확장 */
}

/* Dropdown Menu */
.dropdown {
  position: fixed;
  /* fixed로 설정하여 스크롤에도 고정 */
  top: 110px;
  /* navbar의 높이만큼 떨어지도록 설정 (navbar 높이: 60px) */
  left: 0;
  right: 0;
  z-index: 999;
  /* 드롭다운 메뉴가 navbar 위에 보이도록 설정 */
  display: none;
  /* 기본적으로 숨김 */
  background-color: #333333;
  /* 어두운 배경 */
  color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  /* 그림자 효과 */
  padding-top: 20px;
  padding-bottom: 20px;
  /* 내부 여백 */
  text-align: center;
  /* 텍스트 왼쪽 정렬 */
  border-top: 1px solid #444;
  /* 위쪽에 테두리 추가 */
}

/* Category Item Hover */
.category-item:hover .dropdown {
  display: flex;
  /* Hover 시 드롭다운 메뉴 표시 */
  justify-content: space-between;
  /* 좌우 간격 균등하게 */
}

/* Dropdown Item */
.dropdown-item {
  padding: 10px 0;
  /* 각 항목 내부 여백 */
  font-size: 16px;
  /* 텍스트 크기 */
  color: white;
  /* 텍스트 색상 */
  text-decoration: none;
  cursor: pointer;
  /* 커서 포인터 표시 */
  transition: background-color 0.3s ease;
  /* 배경색 전환 효과 */
}

.dropdown-item:hover {
  color: #f0c040;
  /* Hover 시 텍스트 강조 */
}

.side-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
  height: 100%;
  background-color: white;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.3);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 1000;
  padding: 20px;
}

.side-menu--open {
  transform: translateX(0);
}

.side-menu .close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.side-menu--nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.side-menu h2 {
  margin: 20px 0;
}

.side-menu ul {
  list-style: none;
  text-align: left;
  padding: 0;
}

.side-menu ul li {
  margin: 10px 0;
}

.side-menu ul li a {
  text-decoration: none;
  color: #333;
}

@media (max-width: 940px) {
  .navbar {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .search-bar {
    max-width: 400px;
  }

  .navbar-top-right,
  .navbar-bottom {
    display: none;
    /* 작아지면 숨김 */
  }

  .navbar__hamburger {
    display: block;
    /* 햄버거 버튼 표시 */
  }
}

@media (max-width: 650px) {

  .logo img {
    height: 20px;
  }

  .search-bar {
    display: none;
  }
}


/* 푸터 */
.footer {
  display: flex;
  flex-wrap: wrap;
  /* 화면이 좁아지면 줄바꿈 */
  background-color: #333;
  color: #fff;
  padding-top: 40px;
  padding-bottom: 40px;
  /* 기본 패딩 */
  justify-content: space-between;
  /* 섹션 간 간격 균등 */
  gap: 20px;
  /* 세로 간격 */
  box-sizing: border-box;
  /* 패딩 포함한 박스 크기 */
}

/* 푸터 섹션 */
.footer-section {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  max-width: 200px;
}

.footer-logo {
  width: 50px;
  margin-bottom: 10px;
}

.footer-section p {
  text-align: left;
  margin-bottom: 10px;
}

.footer-section h4 {
  font-size: 16px;
  margin-top: 0px;
  margin-bottom: 30px;
}

.footer-section ul {
  list-style: none;
  padding: 0;
  margin-top: 0;
  text-align: left;
  font-size: 14px;
}

.footer-section ul li {
  margin-bottom: 15px;
  opacity: 0.6;
}

.footer-section.form-section {
  max-width: 267px;
  /* 폼 섹션은 기본 화면에서 267px로 고정 */
  width: 100%;
  /* 반응형을 위해 100% 너비 */
}

/* 폼 스타일 */
.footer-section form {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  padding: 0 16px;
  width: 100%;
  height: 46px;
  box-sizing: border-box;
}

/* 인풋 필드 */
.footer-section input {
  border: none;
  outline: none;
  background: transparent;
  color: white;
  font-size: 13px;
  flex: 1;
}

/* 버튼 */
.footer-section button {
  position: absolute;
  right: 2px;
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 46px;
  height: 46px;
}

.footer-section button img {
  width: 23px;
  height: 15px;
  object-fit: contain;
  opacity: 0.2;
}