.sell-page {
    padding-top: 20px;
    padding-bottom: 20px;
    margin: 0 auto;
    text-align: left;
}

.image-section {
    display: flex;
    flex-direction: row;
    align-items: top;
    margin-bottom: 20px;
}

.image-box {
    position: relative;
    height: 400px;
    width: 300px;
    background-color: #e0e0e0;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin-right: 40px;
}

.image-box img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.placeholder {
    font-size: 14px;
    color: #aaa;
}

.file-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

.instructions {
    flex: 1;
}

.instructions h3 {
    margin-top: 5px;
    font-size: 18px;
    margin-bottom: 10px;
}

.instructions ul {
    margin: 0;
    padding-left: 14px;
    font-size: 14px;
    color: #555;
}

.description-input {
    width: 100%;
    height: 100px;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    box-sizing: border-box;
    font-size: 14px;
    margin-bottom: 20px;
    resize: vertical;
}

.submit-button {
    width: 100%;
    padding: 10px;
    background-color: #DFE3E6;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
}

.submit-button:hover {
    background-color: #f5b536;
}